  const [openCA, setOpenCA] = React.useState(false);

  const handleClickA = () => {
    setOpen(!open);
    setOpenAA(!openAA);
    setOpenAB(false);
    setOpenBA(false);
    setOpenCA(false);
  };
  const handleClickB = () => {
    setOpen(!open);
    setOpenAB(!openAB);
    setOpenAA(true);
    setOpenBA(false);
    setOpenCA(false);
  };
