import React from 'react';
import { makeStyles, Theme, createStyles , fade} from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import IconButton from '@material-ui/core/IconButton';
import Divider from '@material-ui/core/Divider';
import InboxIcon from '@material-ui/icons/Inbox';
import DraftsIcon from '@material-ui/icons/Drafts';

import {menuList} from './data'

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
      backgroundColor: fade(theme.palette.primary.light,0.3),
    },
  }),
);

export default function SelectedListItem({handleSelected}) {
  const classes = useStyles();
  const [selectedIndex, setSelectedIndex] = React.useState(1);

  const handleListItemClick = (
    event: React.MouseEvent<HTMLDivElement, MouseEvent>,
    index: number,
  ) => {
    setSelectedIndex(index);
  };

  return (
    <div className={classes.root}>
      <List component="nav" aria-label="main mailbox folders">
      {menuList.map((item,index) => 
        <ListItem
          button
          onClick={(event) => handleSelected(event, index)}
        >
          <ListItemIcon>
      <IconButton aria-label="delete" className={classes.margin} style={{backgroundImage:
      "linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)",
  }}>
            {item.icon}
        </IconButton>
           
          </ListItemIcon>
          <ListItemText primary={item.title} />
        </ListItem>
        )}
      </List>
    </div>
  );
}