import ButtonGroup from "@material-ui/core/ButtonGroup";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormLabel from "@material-ui/core/FormLabel";

import LinearProgress from "@material-ui/core/LinearProgress";

import {lesson_title, lesson} from './data'
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
    },
    button: {
      marginTop: theme.spacing(1),
      marginRight: theme.spacing(1),
    },
    actionsContainer: {
      marginBottom: theme.spacing(2),
    },
    resetContainer: {
      padding: theme.spacing(0),
    },
    formControlA: {
      margin: theme.spacing(3),
    },
    buttonA: {
      margin: theme.spacing(1, 1, 0, 0),
    },
    rootA: {
      width: "100%",
      "& > * + *": {
        marginTop: theme.spacing(2),
      },
    },
    image:{
      maxWidht: "50vh",
    },buttonNext: {
      marginTop: theme.spacing(1),
      marginRight: theme.spacing(1),
      backgroundImage:
        "linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)"
    },
    
  paper: {
    marginLeft: theme.spacing(0),
    padding: 10,
    display: "flex",
    textAlign: "center",
    alignItems: "center",
    justifyContent: "center",
  },
  })
);

export default function Topic01({theme,selectedItem, handleClose}) {
  const classes = useStyles(theme);
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = React.useState(0);
  const items = lesson[selectedItem].item;
  const [title, setTitle] = React.useState(lesson[selectedItem].title);

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
      <div>
         <div className={classes.paper} style={{backgroundColor: theme.palette.secondary.main}}>
    <Typography variant="h6" color="textDisabled" component="h6">
    {title}
</Typography>
</div> 
      <Stepper activeStep={activeStep} orientation="vertical" className={classes.root}>
         
        {lesson[selectedItem].item.map((obj, index) => (
          <Step key={obj}>
            <StepLabel>
            <Typography variant="subtitle1" color="textDisabled" component="h6" style={{ 
      backgroundImage:
        "linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)"}}>
                {obj}
        </Typography>
        </StepLabel>
            
            <StepContent>
              <Typography
              >{lesson[selectedItem].detail[index]}</Typography>
              
                 <div><img src={lesson[selectedItem].images[index]} alt="" style={{maxWidth: "100%", maxHeight: "60vh"}}></img></div> 
              
                    
                        <ButtonGroup>
                          <Button
                            variant="contained"
                            color="secondary"
                            disabled={activeStep === 0}
                            onClick={handleBack}
                            className={classes.button}
                          >
                            ย้อนกลับ
                          </Button>
                          <Button
                            variant="contained"
                            color="primary"
                            onClick={handleNext}
                            className={classes.button}
                          >
                            {activeStep === items.length -1
                              ? "เสร็จสิ้น"
                              : "ขั้นตอนถัดไป"}
                          </Button>
                        </ButtonGroup>
               
            </StepContent>
          </Step>
        ))}
        {activeStep === items.length && (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography>เสร็จสิ้น {title}&apos; </Typography>
          <Button onClick={handleReset} className={classes.button} variant="contained"
                            color="secondary">
            อ่านอีกครั้ง
          </Button>
          <Button onClick={handleClose} className={classes.button} variant="contained"
                            color="primary">
            เลือกเมนูใหม่
          </Button>
        </Paper>
      )}
      </Stepper>
      </div>
  );
}
