function getStepContent(step: number,setChildComplete) {
  switch (step) {
    case 0:
      return <div></div>;
    case 1:
      return <div></div>;
    default:
      return "ติดต่อครูผู้ดูแลระบบ";
  }
}

function getActiveContent(allTask) {
  switch (allTask) {
    case 0:
      return 0;
    case 1:
      return 1;
    case 2:
      return 2;
    case 3:
      return 3;
      case 4:
        return 4;
    default:
      return 4;
  }
}



export default function UnitA({ theme , checkUnit, isUnit1 }) {
  const classes = useStyles( theme );
  const [checked, setChecked] = React.useState(true);
  const [open, setOpen] = React.useState(false);
  const [dataItems, setDataItems] = React.useState(false);
  const [selectedItem, setSelectedItem] = React.useState(0);
  const [defaultItem, setDefaultItem] = React.useState(false);
  const [preOpen, setPreOpen] = React.useState(0);
  const [count, setCount] = React.useState(0);
  const [isMenu0, setMenu0] = React.useState(false);
  const [isMenu1, setMenu1] = React.useState(false);
  const handleChange = (event) => {
    handleClose();
    if(!isUnit1){
      checkUnit(1);
    } else {
      checkUnit(0);
    }
  };

  const handleSelected = (event, index) => {
    setChecked((prev) => !prev);
    setOpen(checked);
    setSelectedItem(index);
    setDefaultItem(true);
  }

  const handleClose = () => {
    setChecked((prev) => !prev);
    setOpen(checked);
    setDefaultItem(false);
  }

  return (
    <div>
    <Card className={classes.root} key="unitA" id={1}>
      <CardActionArea onClick={handleChange} className={classes.rootHeader} >
      <Typography variant="h5" color="textSecondary" component="h5" style={{padding: 10}}>
              เครื่องมือช่วยเหลือสำหรับการใช้งาน Google Classroom
              </Typography>
        </CardActionArea>
          <Collapse in={checked} timeout="auto" unmountOnExit>
            <CardContent className={classes.rootBody}>
              <Typography variant="body2" color="textSecondary" component="p">
                <Paper style={{ maxWidth: "100vw" }}>
                  <SelectedListItem handleSelected={handleSelected}></SelectedListItem>
                </Paper>
              </Typography>
            </CardContent>
          </Collapse>
      <CardActionArea onClick={handleChange} className={classes.rootFooter} >
        <IconButton color="#FFF">
          {checked ? <Typography variant="h6" color="textSecondary" component="h6">
              ย้อนกลับ
              <ExpandLess /> </Typography> : <Typography variant="h6" color="textSecondary" component="h6">
              เลือกเมนูที่ต้องการ<ExpandMore /></Typography>}
        </IconButton>
      </CardActionArea>
    </Card>
</div>
  );
}
